.full-form-open,
.todo-form {
    display: -ms-flexbox;
    display: flex;
    background-color: #FAFAFA;
    border-top: 1px solid rgba(0,0,0,.1);
    padding: 10px;
}

.add-todo-button-title {
text-align: right;
    margin-left: auto;
    height: 32px;
    line-height: 32px;
}

.todo-form input {
    -ms-flex: 1 1;
        flex: 1 1;
    outline: none;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
}

.todo-form input:focus {
    border: 1px solid #00d8ff;
}

.todo-form button {
    margin-left: 10px;
}

.full-form-open-bot {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    font-size: 1rem;
    padding: .2em;
}




